<template lang="html">
    <div class="container">
        <div class="row">
            <div class="col-md-12">
                <h3>Estas seguro de eliminar este Libro</h3>
                <p>
                    Titulo : {{ this.element.title}}
                </p>
                <p>
                    Titulo : {{ this.element.description}}
                </p>                
            </div>
            <div class="col-md-12">
                <b-button v-on:click="deleteBook" variant="danger">Eliminar</b-button>
                <b-button type="submit" variant="primary" :to="{name:'ListBook'}">Cancelar</b-button>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
import swal from 'sweetalert'

    export default {
        data(){
            return{
                bookId: this.$route.params.bookId,
                element: {
                    title:'',
                    description:''
                }
            }
        },
        methods:{
            getBook(){
                const path = `${process.env.BASE_URI}books/${this.bookId}/`
                axios.get(path).then((response) => {
                    this.element.title = response.data.title
                    this.element.description = response.data.description
                })
                .catch((error) => {
                    console.log(error)
                })
            },
            deleteBook(){
                const path = `${process.env.BASE_URI}books/${this.bookId}/`
                axios.delete(path).then((response) => {
                    location.href = '/books',
                    swal("Libro eliminado exitosamente", "", "success")
                })
                .catch((error) => {
                    swal("No es posible eliminar el libro.", "", "Error")
                })
            }
        },
        created(){
            this.getBook()
        }
    }
</script>

<style scoped>

</style>


