<template lang="html">
    <div class="container">
        <div class="row">
            <div class="col text-left">
                <h2>Listado Libros</h2>
                <div class="col-md-12">
                    <b-button size="sm" variant="primary" :to="{ name:'NewBook' }">Nuevo Libro</b-button>
                </div>
                <div class="col-md-12">
                    <b-table striped hover :items="books" :fields="fields">
                        <template slot="action" slot-scope="data    ">
                            <b-button size="sm" variant="primary" :to="{ name:'EditBook', params:{bookId: data.item.id}}">Editar</b-button>
                            <b-button size="sm" variant="danger" :to="{ name:'DeleteBook', params:{bookId: data.item.id}}">Eliminar</b-button>
                        </template>                     
                    </b-table>
                </div>
                
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios'

export default {
    data() {
        return {
            fields:[
                { key:'title', label:'Titulo' },
                { key:'description', label:'Descripcion'},
                { key:'action', label:'Accion'}
            ],
            books:[]
        }
    },
    methods: {
        getBooks(){
            const path = `${process.env.BASE_URI}books/`
            axios.get(path).then((response) => {
                this.books = response.data
            })
            .catch((erro) => {
                console.log(erro)
            })
        }
    },

    created() {
        this.getBooks()
    },

}
</script>

<style lang="css" scopec>

</style>


