<template>
    <div class="container">
       <div class="row">
          <div class="col-md-12">
            <div class="card">
              <div class="card-body">
                <h3 class="card-title">Editar</h3>
                <p class="card-text">los datos del libro.</p>
                <form @submit="onSubmit">
                    <div class="form-group">
                      <label for="title">Titulo del libro</label>
                      <input type="text" name="title" id="" class="form-control" placeholder="titulo" aria-describedby="helpId" v-model.trim="form.title">
                      <small id="helpId" class="text-muted">nombre del titlo</small>
                    </div>
                    <div class="form-group">
                      <label for="description">Descripcion</label>
                      <textarea class="form-control" name="descripcion" id="descripcion" rows="4" v-model.trim="form.description"></textarea>
                      <small id="helpId" class="text-muted">breve descripcion del libro</small>
                    </div>
                    <div class="row">
                        <div class="col-md-12 text-left">
                            <b-button type="submit" variant="outline-primary">Guardar</b-button>
                            <b-button type="submit" variant="success" :to="{name:'ListBook'}">Cancelar</b-button>
                        </div>
                    </div>
                </form>
              </div>
            </div>
          </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
import swal from 'sweetalert'

    export default {
      data(){
        return{
          bookId: this.$route.params.bookId,
          form:{
            title: '',
            description: ''
          }
        }
      },
      methods:{
        onSubmit(event){
          event.preventDefault()

          const path = `${process.env.BASE_URI}books/${this.bookId}/`

          axios.put(path, this.form).then((response) => {
            this.form.title = response.data.title
            this.form.description = response.data.description
            swal("Libro actualizado exitosamente", "", "success")
          })
          .catch((error) => {
            console.log(error)
          })
        },
        getBook(){
          const path = `${process.env.BASE_URI}books/${this.bookId}/`
          axios.get(path).then((response) => {
            this.form.title = response.data.title
            this.form.description = response.data.description
          })
          .catch((error) => {
            console.log(error)
          })
        }
      },
      created(){
        this.getBook()
      }
    }
</script>

<style scoped>

</style>


